import React from 'react';
import Gallery from 'react-grid-gallery';
// import styles from 'react-sleek-photo-gallery/dist/style.css';

import Img1 from '../img/projThumbs/proj1.jpg';
import Img2 from '../img/projThumbs/proj2.jpg';
import Img3 from '../img/projThumbs/proj3.jpg';
import Img4 from '../img/projThumbs/proj4.jpg';

const ProjectDetails = () => {


  const containerStyles = {
    display: 'inline-block',
    maxHeight: '60em',
    margin: '0em',
    marginBottom: "8em",
    // backgroundColor: '#1F1F1F',
  }
  const textCont = {
    width: '90%',
    maxWidth: '45em',
    margin: '6em auto',
    textAlign: 'left',
  }

  const galleryCont = {
    display: 'block',
    minHeight: '1em',
    width: '100%',
    overflow: 'auto',
    // height: "100%",
    margin: 'auto',
    textAlign: 'center'
  }

  const IMAGES =[
    {
      // src: "https://c2.staticflickr.com/9/8817/28973449265_07e3aa5d2e_b.jpg",
      thumbnail: Img1,
      thumbnailWidth: 336,
      thumbnailHeight: 188,
      caption: "After Rain (Jeshu John - designerspics.com)"
    },
    {
      // src: "https://c2.staticflickr.com/9/8356/28897120681_3b2c0f43e0_b.jpg",
      thumbnail: Img2,
      thumbnailWidth: 336,
      thumbnailHeight: 188,
      caption: "Boats (Jeshu John - designerspics.com)"
    },

    {
      // src: "https://c4.staticflickr.com/9/8887/28897124891_98c4fdd82b_b.jpg",
      thumbnail: Img3,
      thumbnailWidth: 336,
      thumbnailHeight: 188,
    },
    {
      // src: "https://c4.staticflickr.com/9/8887/28897124891_98c4fdd82b_b.jpg",
      thumbnail: Img4,
      thumbnailWidth: 336,
      thumbnailHeight: 188,
    },
    {
      // src: "https://c2.staticflickr.com/9/8817/28973449265_07e3aa5d2e_b.jpg",
      thumbnail: Img1,
      thumbnailWidth: 336,
      thumbnailHeight: 188,
      caption: "After Rain (Jeshu John - designerspics.com)"
    },
    {
      // src: "https://c2.staticflickr.com/9/8356/28897120681_3b2c0f43e0_b.jpg",
      thumbnail: Img2,
      thumbnailWidth: 336,
      thumbnailHeight: 188,
      caption: "Boats (Jeshu John - designerspics.com)"
    },

    {
      // src: "https://c4.staticflickr.com/9/8887/28897124891_98c4fdd82b_b.jpg",
      thumbnail: Img3,
      thumbnailWidth: 336,
      thumbnailHeight: 188,
    },
    {
      // src: "https://c4.staticflickr.com/9/8887/28897124891_98c4fdd82b_b.jpg",
      thumbnail: Img4,
      thumbnailWidth: 336,
      thumbnailHeight: 188,
    },
  ]

  return (

    <div style={ containerStyles }>
      <div style={ textCont }>
        <h2>Project Name Stuff</h2>
        <p>If there is one thing kids love more than eating pizza, it’s being able to make it themselves </p>
      </div>
      <div style={ galleryCont }>
        <Gallery
          images={IMAGES}
          enableImageSelection={false}
          enableLightbox={false}
        />
      </div>

    </div>
  )
}

export default ProjectDetails;



